import React, { useState } from 'react';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Card from '@vkontakte/vkui/dist/components/Card/Card';
import Gallery from '@vkontakte/vkui/dist/components/Gallery/Gallery';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Icon24Place from '@vkontakte/icons/dist/24/place';
import bridge from '@vkontakte/vk-bridge';

import './Style.css';
import iosPng from '../img/ios.png';
import androidPng from '../img/android.png';


const Warning = props => {
    const [popout, setStatePopout] = useState(null)
    const [data, setStateData] = useState()

    const checkOnGps = () => {
        setStatePopout(<div className="earthPop"></div>)
        bridge.send("VKWebAppGetGeodata")
            .then(data => {
                setStateData(data.lat)
                console.log(data.lat);
                if (isNaN(data.lat)) {
                    console.log("не полу");
                    setStatePopout("Данные геопозиции не получены, попробуйте еще раз.")
                    setTimeout(() => {
                        setStatePopout()
                    }, 1500);
                } else {
                    props.goToPage('Home')
                    console.log("получ");
                    setStatePopout()
                }
            })
            .catch(error => {
                
                setStatePopout("Что-то пошло не так.")

            })
        if (isNaN(data)) {
            console.log("не полу");
            
        } else {
            props.goToPage('Home')
            console.log("получ");
        }
    }

    return (
        <Panel id={props.id} separator={false} >
            <PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
        </PanelHeader>
            <div className="stage">
                <div className="titleGeoCheck"><h3>Для работы приложения, включите службы геолокации на устройстве.</h3></div>
                
                <div className="popout">
                    
                    <Gallery
                        slideWidth="100%"
                        align="center"
                        bullets="dark"
                        style={{ height: 350, borderRadius: "55px 55px 0px 0px" }}
                    >
                        <img src={iosPng} />
                        <img src={androidPng} />
                    </Gallery>
                    <p className="textPopout">{popout}</p>
                </div>
                <div></div>
                <div className="geoCheckDiv">
                    
                <Button mode="outline" size="xl" onClick={checkOnGps} >Проверить подключение</Button>
                </div>
            </div>
    </Panel>
)};

export default Warning;