import React, { useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';

const Loader = props => {

    useEffect(() => {
        
        bridge.send("VKWebAppStorageGet", { "keys": ["isSeenIntro1"] })
            .then(data => {
                console.log(Object.values(data.keys[0])[1]);
                if (Object.values(data.keys[0])[1] === "true") {

                    bridge.send("VKWebAppGetGeodata")
                        .then(data => {
                            if (isNaN(data.lat)) {
                                console.log(data.lat);
                                props.goToPage('Warning')
                            } else {
                                props.goToPage('Home')
                            }
                        })
                        .catch(error => {
                            console.log(error);
                            props.goToPage('Warning')
                        })

                } else {
                    props.goToPage('Intro')
                }
            })
            .catch(error => {
                console.log(error)
            })


    }, []);

   

    return (

    <Panel id={props.id} separator={false}>
        <div className="stage">
            <div className="blockLoader">
                <div className="titleLoader"><h1>Идет загрузка.</h1></div>
                <div className="earthLoader"></div>
            </div>
        </div>
    </Panel>
    )
};

export default Loader;