import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ConfigProvider from '@vkontakte/vkui/dist/components/ConfigProvider/ConfigProvider';
import '@vkontakte/vkui/dist/vkui.css';

import Loader from './panels/Loader';
import Intro from './panels/Intro';
import Home from './panels/Home';
import Play from './panels/Play';
import Settings from './panels/Settings';
import Rating from './panels/Rating';
import Info from './panels/Info';
import Warning from './panels/Warning';

const lights = ['bright_light', 'client_light'];

const App = () => {

	const [activePanel, setActivePanel] = useState("Home"); 
	const [history, setHistory] = useState(['Home']) 

	

	

	

	function goToPage(name) { 
		window.history.pushState({ panel: name }, name); 
		setActivePanel(name); 
		history.push(name); 
	};

	const goBack = () => {
			if (history.length === 1) {
				bridge.send("VKWebAppClose", { "status": "success" });
			} else if (history.length > 1) {
				history.pop()
				setActivePanel(history[history.length - 1])
			}
		
	}

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data } }) => {
			if (type === 'VKWebAppUpdateConfig') {
				camelCase(data.scheme)
			}
		});
		window.addEventListener('popstate', () => goBack()); 
	}, []);

	const [scheme, SetStateScheme] = useState('space_gray');

	function camelCase(scheme, needChange = false) {
		let isLight = lights.includes(scheme);

		if (needChange) {
			isLight = !isLight;
		}
		SetStateScheme(isLight ? 'bright_light' : 'space_gray');

		bridge.send('VKWebAppSetViewSettings', {
			'status_bar_style': isLight ? 'dark' : 'light',
			'action_bar_color': isLight ? '#ffffff' : '#191919'
		});
	}

	

	
	
	

	return  ( 
		
		<ConfigProvider 
			isWebView={true}
			scheme={scheme}>
			<View
			
				activePanel={activePanel}
				history={history}
				scheme={scheme}
				onSwipeBack={goBack}
			>
				<Loader id="Loader"
					goToPage={goToPage}
					scheme={scheme}
					camelCase={camelCase}

				/>
				<Intro id="Intro"
					goToPage={goToPage}
					scheme={scheme}
					camelCase={camelCase}/>
				<Home id="Home"
					goToPage={goToPage}
					camelCase={camelCase}
					scheme={scheme}

				/>
				<Play
					id="Play"
					goToPage={goToPage}
					scheme={scheme}
					camelCase={camelCase}


				/>
				<Settings
					id="Settings"
					camelCase={camelCase}
					scheme={scheme}
				/>
				<Rating
					id="Rating"
				/>
				<Info
					id="Info"
				/>
				<Warning
					scheme={scheme}
					camelCase={camelCase}
					id="Warning"
					goToPage={goToPage}

				/>
			</View>
		</ConfigProvider>
	);
}

export default App;