import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Progress from '@vkontakte/vkui/dist/components/Progress/Progress';
import Icon32Place from '@vkontakte/icons/dist/32/place';

import './Style.css';

const arrCityLeft = [
	[["city", "Уфа"], ["lat", 54.7431], ["long", 55.9678]],
	[["city", "Шанхай"], ["lat", 31.23042], ["long", 121.4737]],
	[["city", "Мехико"], ["lat", 19.4285], ["long", -99.1277]],
	[["city", "Сан-Паулу"], ["lat", -23.55052], ['long', -46.63331]],
	[["city", "Каир"], ["lat", 30.0626], ["long", 31.2497]],
	[["city", "Мумбаи"], ["lat", 19.07598], ["long", 72.87766]],
	[["city", "Дакка"], ["lat", 23.81033], ["long", 90.41252]],
	[["city", "Осака"], ["lat", 34.6812515], ["long", 135.5314026]],
	[["city", "Карачи"], ["lat", 24.86146], ["long", 67.00994]],
	[["city", "Стамбул"], ["lat", 41.0138], ["long", 28.9497]],
	[["city", "Москва"], ["lat", 55.7522], ["long", 37.6156]],
];

const arrCityRight = [
	[["city", "Уфа"], ["lat", 54.7431], ["long", 55.9678]],
	[["city", "Шанхай"], ["lat", 31.23042], ["long", 121.4737]],
	[["city", "Мехико"], ["lat", 19.4285], ["long", -99.1277]],
	[["city", "Сан-Паулу"], ["lat", -23.55052], ['long', -46.63331]],
	[["city", "Каир"], ["lat", 30.0626], ["long", 31.2497]],
	[["city", "Мумбаи"], ["lat", 19.07598], ["long", 72.87766]],
	[["city", "Дакка"], ["lat", 23.81033], ["long", 90.41252]],
	[["city", "Осака"], ["lat", 34.6812515], ["long", 135.5314026]],
	[["city", "Карачи"], ["lat", 24.86146], ["long", 67.00994]],
	[["city", "Стамбул"], ["lat", 41.0138], ["long", 28.9497]],
	[["city", "Москва"], ["lat", 55.7522], ["long", 37.6156]],
];


const Play = (props) => {
	const [leftCity, setStateLeftCity] = useState(" ");
	const [rightCity, setStateRightCity] = useState(" ");
	const [count, setStateCount] = useState(100);
	const [timer, setStateTimer] = useState();

	const [leftCityDictance, setStateleftCityDictance] = useState(" ");
	const [rightCityDictance, setStaterightCityDictance] = useState(" ");
	const [leftCityResponse, setStateleftCityResponse] = useState(0);
	const [rightCityResponse, setStaterightCityResponse] = useState(0);
	const [statusBackground, setStatestatusBackground] = useState("stage");
	const [isStartGame, setStateisStartGame] = useState(false)
	const [isOverGame, setStateisOverGame] = useState(false)
	const [isNextGame, setStateisNextGame] = useState(false)
	const [timerId, setStatetimerId] = useState("timer")
	const [progressId, setStateprogressId] = useState("progressNone")
	const [resultId, setStateresultId] = useState("result")
	const [result, setStateresult] = useState()
	
	const [userPostRes, setStateuserPostRes] = useState(false)
	const [geoUser, setStateGeoUser] = useState(<br/>);
	const [geoCheck, setStateGeoCheck] = useState(" ");
	const [isGetGeo, setStateIsGetGeo] = useState(null);

const nextGame = () => {
	

	if (isNextGame === false) {
		setStateisNextGame(true)
		startGame()
	} else {
		
	}

}
	useEffect(() => {
		startGame()
		if (isNextGame === false) {
			setStateisNextGame(true)
		} else {

		}
	}, []);

	const startGame = () => {
		setStateTimer()
		setStatetimerId("timer")
		setStateprogressId("progress")
		setStateresultId("resultNone")
		setStatestatusBackground("stage")
		setStateisOverGame(false)
		setStateisStartGame(false)
		setStaterightCityDictance();
		setStateleftCityDictance();
		setStateTimer();
		bridge.send("VKWebAppGetGeodata")
			.then(data => {
				
				var url = "https://suggestions.dadata.ru/suggestions/api/4_1/rs/geolocate/address";
				var token = "8542806bad4af5ebade0e922834e3e32cd380a2a";
				var query = { lat: data.lat, lon: data.long, count: 1 };
				var options = {
					method: "POST",
					mode: "cors",
					headers: {
						"Content-Type": "application/json",
						"Accept": "application/json",
						"Authorization": "Token " + token
					},
					body: JSON.stringify(query)
				}

				if (isNaN(data.lat)) {

					props.goToPage('Warning')

				} 

				fetch(url, options)
					.then(response => response.text())
					.then(result => {
						const geo = Object.values(Object.entries(JSON.parse(result))[0][1][0])[0]
						setStateGeoUser(geo)
					})
					.catch(error => {
						setStateGeoUser("Широта " + data.lat + ", " + "Долгота " + data.long)
						
					})
			})
			.catch(error => {

			})
		

		const timerId = setInterval(() => {
			

			var randRight = Math.floor(Math.random() * arrCityRight.length);
			var randLeft = Math.floor(Math.random() * arrCityLeft.length);
			let arrLeft = Object.fromEntries(arrCityLeft[randLeft]);
			let arrRight = Object.fromEntries(arrCityRight[randRight]);
			setStateRightCity(arrRight.city)
			setStateLeftCity(arrLeft.city)

		}, 60);

		setTimeout(() => { clearInterval(timerId); countDown(); setStateisStartGame(true) }, 3000);

	}

	const countDown = () => {
		

		var randRightCity = Math.floor(Math.random() * arrCityRight.length);
		let arrRight = Object.fromEntries(arrCityRight[randRightCity]);

		var randLeftCity = Math.floor(Math.random() * arrCityLeft.length);
		let arrLeft = Object.fromEntries(arrCityLeft[randLeftCity]);

		setStateRightCity(arrRight.city)
		setStateLeftCity(arrLeft.city)

		bridge.send("VKWebAppGetGeodata")
			.then(data => {
				var url = "https://suggestions.dadata.ru/suggestions/api/4_1/rs/geolocate/address";
				var token = "8542806bad4af5ebade0e922834e3e32cd380a2a";
				var query = { lat: data.lat, lon: data.long, count: 1 };
				var options = {
					method: "POST",
					mode: "cors",
					headers: {
						"Content-Type": "application/json",
						"Accept": "application/json",
						"Authorization": "Token " + token
					},
					body: JSON.stringify(query)
				}

				fetch(url, options)
					.then(response => response.text())
					.then(result => {

						const geo = Object.values(Object.entries(JSON.parse(result))[0][1][0])[0]
						setStateGeoUser(geo)
					})
					.catch(error => {
						setStateGeoUser("Широта " + data.lat + ", " + "Долгота " + data.long)
					})

				function getDistanceFromLatLonInKmRight(lat4, lon4, lat8, lon8) {
					var R1 = 6371;
					var dLat1 = deg2rad(lat8 - lat4);
					var dLon1 = deg2rad(lon8 - lon4);
					var a1 =
						Math.sin(dLat1 / 2) * Math.sin(dLat1 / 2) +
						Math.cos(deg2rad(lat4)) * Math.cos(deg2rad(lat8)) *
						Math.sin(dLon1 / 2) * Math.sin(dLon1 / 2)
						;
					var c1 = 2 * Math.atan2(Math.sqrt(a1), Math.sqrt(1 - a1));
					var d1 = R1 * c1;
					return d1;
				}
				function deg2rad(deg1) {
					return deg1 * (Math.PI / 180)
				}
				setStaterightCityResponse(+getDistanceFromLatLonInKmRight(data.lat, data.long, arrRight.lat, arrRight.long).toFixed(0))
				function getDistanceFromLatLonInKmLeft(lat1, lon1, lat2, lon2) {
					var R = 6371;
					var dLat = deg2rad(lat2 - lat1);
					var dLon = deg2rad(lon2 - lon1);
					var a =
						Math.sin(dLat / 2) * Math.sin(dLat / 2) +
						Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
						Math.sin(dLon / 2) * Math.sin(dLon / 2)
						;
					var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
					var d = R * c;
					return d;
				}

				function deg2rad(deg) {
					return deg * (Math.PI / 180)
				}
				setStateleftCityResponse(+getDistanceFromLatLonInKmLeft(data.lat, data.long, arrLeft.lat, arrLeft.long).toFixed(0))

				let i = 100
				let t = 4
				let timerIdcountDown = setInterval(() => {
					

					setStateCount(i = i - 20)
					setStateTimer("Осталось " + t-- + " сек")
					if (i === 0) {
						setStateTimer("Не успели!")
						
						
					} else {
						
					}

				}, 1000);

				setTimeout(() => {
					
					
					setStateisOverGame(true)
					setStateisNextGame(false)
					clearInterval(timerIdcountDown);
					setStateCount(100);
					setStaterightCityDictance(getDistanceFromLatLonInKmRight(data.lat, data.long, arrRight.lat, arrRight.long).toFixed(0) + " км");
					setStateleftCityDictance(getDistanceFromLatLonInKmLeft(data.lat, data.long, arrLeft.lat, arrLeft.long).toFixed(0) + " км");
				}, 5000);

				
			});
	}


	const userResponseLeft = () => {
		if (isOverGame === false) {
			if (isStartGame === true) {

				if (leftCityResponse < rightCityResponse) {
					setStatestatusBackground("stageSucces")
					setStateresult("Верно!")
					setTimeout(() => {
						setStatestatusBackground("stage")
					}, 2000);
				} else {
					setStateresult("Неверно!")
					setStatestatusBackground("stageError")
					setTimeout(() => {
						setStatestatusBackground("stage")
					}, 2000);

				}
				setStaterightCityDictance((rightCityResponse).toFixed(0) + " км");
				setStateleftCityDictance((leftCityResponse).toFixed(0) + " км");
				setStateisOverGame(true)
				setStateuserPostRes(true)
				setStatetimerId("timerNone")
				setStateprogressId("progressNone")
				setStateresultId("result")
			} else {

			}
} else {
	
}
		

		
	}
	const userResponseRight = () => {
		if (isOverGame === false) {
			if (isStartGame === true) {
				if (leftCityResponse > rightCityResponse) {
					setStateresult("Верно!")
					setStatestatusBackground("stageSucces")
					setTimeout(() => {
						setStatestatusBackground("stage")
					}, 2000);
				} else {
					setStatestatusBackground("stageError")
					setStateresult("Неверно!")
					setTimeout(() => {
						setStatestatusBackground("stage")
					}, 2000);


				}
				setStaterightCityDictance((rightCityResponse).toFixed(0) + " км");
				setStateleftCityDictance((leftCityResponse).toFixed(0) + " км");
				setStateisOverGame(true)
				setStateuserPostRes(true)
				setStatetimerId("timerNone")
				setStateprogressId("progressNone")
				setStateresultId("result")

			} else {

			}
		} else {
			
		}
		
		
	}


	return (
		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
        </PanelHeader>
			<div className={statusBackground}>


				<div className="topBlock">
					<div className="titlePlay">
						<h1 style={{ color: "white", fontSize: 35, fontFamily: 'Neucha' }}>Какой город <br /> ближе?</h1></div>
					<Icon32Place className="iconPlace" width={25} height={25} />
					<div className="earth"></div>
						<div className="userCityName"><h1 style={{ color: "white", fontSize: 15, fontFamily: 'Neucha', textAlign: "center" }}>{geoUser}</h1></div>
					</div>
					<div className="blockCities">
					<div className="cityDivLeft"><Button  className="cityButtonLeft" onClick={userResponseLeft} size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h1>{leftCity}<br />{leftCityDictance}</h1></Button></div>
					<div className="cityDivRight"><Button  className="cityButtonRight" onClick={userResponseRight} size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h1>{rightCity}<br />{rightCityDictance}</h1></Button></div>
					</div>


				<div className="blockProgress">
					<div className="progressItems">
						<div className={resultId}><h1 style={{ color: "white", fontSize: 30, fontFamily: 'Neucha', textAlign: "center" }}>{result}</h1></div>

						<div className={progressId}><Progress style={{ background: "red" }} value={count} />
							<div className={timerId}><h1 style={{ color: "white", fontSize: 30, fontFamily: 'Neucha', textAlign: "center" }}>{timer}</h1></div>
						</div>
						<div className="nextDivLevel"><Button  className="nextButtonLevel" onClick={nextGame} size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h1>Дальше</h1></Button></div>
					</div>


				</div>
									
				
			</div>
		</Panel>
	)
	
};

export default Play;