import React, { useState } from 'react';
import bridge from '@vkontakte/vk-bridge';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Gallery from '@vkontakte/vkui/dist/components/Gallery/Gallery';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Placeholder from '@vkontakte/vkui/dist/components/Placeholder/Placeholder';

const Intro = props => {
    const [index, setStateIndex] = useState(0)

    const nextSlide = () => {
        console.log("sd");
        setStateIndex(1)
    }

    const firstuse = () => {
        props.goToPage('Loader')
        bridge.send("VKWebAppStorageSet", { "key": "isSeenIntro1", "value": true })
        .then(data => {
            console.log(data);
        })
    }



    return (

        <Panel id={props.id} separator={false}>
            <div className="stage">
                <Gallery
                    slideWidth="100%"
                    style={{ height: "100%" }}
                    bullets="dark"
                    slideIndex={index}
                    onChange={nextSlide}
                >
                    <Placeholder><Button onClick={nextSlide} size="xl" >Приступить!</Button></Placeholder>
                    <Placeholder><Button onClick={nextSlide} size="xl" mode="secondary">Приступить!</Button></Placeholder>
                    <Placeholder><Button onClick={firstuse} size="xl" mode="secondary">Приступить!</Button></Placeholder>
                </Gallery>
            </div>
        </Panel>
    )
};

export default Intro;