import React from 'react';
import bridge from '@vkontakte/vk-bridge';

import {Panel, Button} from '@vkontakte/vkui/';
import Icon48Play from '@vkontakte/icons/dist/48/play';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon28GlobeOutline from '@vkontakte/icons/dist/28/globe_outline';
import Icon28ErrorOutline from '@vkontakte/icons/dist/28/error_outline';

import './Style.css';

const Home = (props) => {

	const startGame = () => {

		bridge.send("VKWebAppGetGeodata")

		.then(data=>  {

			if (isNaN(data.lat)) {

				props.goToPage('Warning')

			} else {

				props.goToPage('Play')

			}
		})
		.catch(error=> {

			console.log(error + " Ошибка при получение геоданных");
			props.goToPage('Warning')

		})

		
	}

	return ( 
		<Panel id={props.id} separator={false}>
				
			<div className="stage">
				<div className="sun"></div>
				<div className="grass"></div>
				<div className="title"><h1 style={{ color: "white", fontSize: 45, fontFamily: 'Neucha' }}>Town</h1></div>
				<Button className="startButton" before={<Icon48Play style={{ color: "white" }} />} mode="outline" size="xl" onClick={() => startGame()}>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',
					}}>Играть!</p>

				</Button>
				<div className="blockButton">
				<Button
					className="settingsButton"
					before={<Icon28SettingsOutline width={41} height={41} style={{ color: "white" }} />}
					mode="outline"
					size=""
					onClick={() => props.goToPage('Settings')}
				/>
				<Button
					className="ratingButton"
					before={<Icon28GlobeOutline width={41} height={41} style={{ color: "white" }} />}
					mode="outline"
					size=""
					onClick={() => props.goToPage('Rating')}
				>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',

					}}></p>
				</Button>
				<Button
					className="aboutButton"
					before={<Icon28ErrorOutline width={41} height={41} style={{ color: "white" }} />}
					mode="outline"
					data-to="persik"
					size=""
					onClick={() => props.goToPage('Info')}

				>
					<p style={{
						color: "white",
						fontSize: 30,
						fontFamily: 'Neucha',

					}}></p>
				</Button>
				</div>
			</div>
			</Panel>
)};

export default Home;
